.button{
  max-width: 100px;
  height: 100px;
  display: flex;
  justify-content: center;
  align-items: center;
}
.parent{
  display: flex;
  margin-top: 50px;
  justify-content: center;

}
.buttonpad{
  /* border: 1px solid black; */
  
  width: 75vw;
  max-width: 500px;
  display: flex;
  flex-wrap: wrap;
  justify-content: space-between;
  padding: 10px;
  margin: 0 auto;
  
}
.equalbutton{
  display: flex;
  justify-content: center;
  align-items: center;
  width: 65%;
  height: 100px;
  font-size: 30px;
  flex:1 1auto;
  margin-top: 5px;
}
.numberdisplay{
  border:1px solid black;
  width: 93%;
  
  
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding: 15px;
  flex-wrap: wrap;

}
.functionbutton{
  
    display: flex;
    justify-content: center;
    align-items: center;
    width: 90%;
    height: 100px;
    font-size: 30px;
    margin-top: 5px;


}
.onlyleft{
  margin-left: 0px;
  width: 70%;
  display: flex;
  flex-wrap: wrap;
  justify-content: space-between;
}
.onlyright{
  margin-right: 5%;
  display: flex;
  width: 20%;
  flex-wrap: wrap;
  
}
.clearbutton{
  /* display: flex;
  align-self:flex-end; */
  /* float: right; */
  height: 150px;
  width: 30%;
  
  height: 100px;
  font-size: 30px;
}
.resultparent{
  width: 70%;
  display: flex;
  flex-direction: column;
}
.currentcalcs{
  
  width: 70%;
  opacity: 0.5;
  font-size: 20px;
  margin-bottom: 50px;
}
.result{
  
  font-size: 45px;
  
  display: flex;
  flex-wrap: wrap;
}

